<template>
  <BaseLayout title="我的账号">
    <template #extra>
      <van-button v-if="inWechat" v-show="authorized" icon="sort" :style="{width:'100%', borderRadius:'10px 0 0 10px', color: '#F60'}" color="#F6F6F6" @click="state.popup=true">切换账号</van-button>
    </template>
    
    <div class="info">
      <van-field v-for="(item,key) in fields" :key="key" :label="item.name" v-model="fields[key].value" clickable input-align="right" :right-icon="fields[key].icon" @click-input="fields[key].icon='edit'" @blur="onChange(key)"></van-field>
      <van-cell title="最近登录时间" :value="profile.login_dt"/>
      <van-cell title="最近登录地址" :border="false" :value="profile.login_ip"/>
      <van-button v-show="inWechat" block class="btn" type="primary" plain @click="onUnbind">解绑账号</van-button>
      <van-button block class="btn" type="primary" to="/logout">退出登录</van-button>
    </div>

    <van-action-sheet v-model:show="state.popup" :actions="state.list" description="当前微信绑定如下账号，点击进行切换" cancel-text="绑定新账号" close-on-click-action @select="onSwitch" @cancel="onBind"/>

  </BaseLayout>
</template>

<style scoped>
.info {
  margin: 10px;
  height: 100px;
  background: #F3F3F3;
  border-radius: 5px;
}
.btn {
  margin:20px 0;
}
</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  data() {
    const fields = reactive({
      mobile: {
        name: "手机号",
        icon: "arrow",
        value: "",
      },
      username: {
        name: "用户名",
        icon: "arrow",
        value: "",
      },
      password: {
        name: "密码",
        icon: "arrow",
        value: "",
      },
      nickname: {
        name: "昵称",
        icon: "arrow",
        value: "",
      },
    });
    const state = reactive({
      popup: false,
      list: [],
    });
    return {fields,state};
  },

  created() {
    this.$init(this.reset);
    this.state.list = this.profile.account;
  },

  computed: {
    inWechat() {
      return this.profile.openid.length > 10;
    },

    ...mapState(["profile"]),
    ...mapGetters(["authorized", "uid", "mobile", "nickname"]),
  },

  methods: {
    reset() {
      this.fields.mobile.value = this.mobile;
      this.fields.username.value = this.profile.username;
      this.fields.nickname.value = this.nickname;
      this.fields.password.value = "******";
    },
    onChange(field) {
      this.fields[field].icon = "arrow";
      if (field == "mobile"   && this.fields.mobile.value == this.mobile) return;
      if (field == "username" && this.fields.username.value == this.profile.username) return;
      if (field == "nickname" && this.fields.nickname.value == this.nickname) return;
      if (field == "password" && this.fields.password.value == "******") return;

      let api = "/user/change";
      let params = {what: field, value: this.fields[field].value};
      if (!/(mobile|username|password)/.test(field)){
        api = "/user/modify";
        params[field] = this.fields[field].value;
      }
      this.$http.post(api, params).then(() => {
        this.$init(this.reset);
      }).catch(() => {
        this.reset();
        this.fields[field].icon = "edit";
      });
    },
    onSwitch(item) {
      this.$http.post("/wechat/switch?u=" + item.id).then((data) =>{
        this.auth(data);
        this.$init(this.reset);
      });
    },
    onBind(){
      this.$router.push("/bind");
    },
    onUnbind() {
      this.$dialog.confirm({
        title: "",
        message: "确认解除账号 " + this.mobile + " 和微信（ID:" + this.profile.openid + "）的绑定关系？",
        theme: "round-button",
      }).then(() => {
          this.$http.post("/wechat/unbind");
      });
    },

    ...mapMutations(["auth"]),
  }
}
</script>
