<template>
  <div class="container">
    
    <van-sticky @change="state.isTop = !state.isTop" v-show="!nonavbar">
      <van-nav-bar class="navbar" :class="{notTop: !state.isTop, noTitle: notitle}" :border="false" @click-left="onClickLeft" @click-right="$emit('click-more')">
        <template #left>
          <div v-show="!noback"><slot name="back"><van-icon name="arrow-left" size="large"></van-icon></slot></div>
        </template>
        <template #title>
          <div :style="{width:'100%'}"><slot name="tool">{{state.isTop ? '' : title}}</slot></div>
        </template>
        <template #right>
          <slot name="more"></slot>
        </template>
      </van-nav-bar>
    </van-sticky>

    <div class="page" :class="{notTop: !state.isTop, noNavbar: nonavbar, noTitle: notitle, hasBar: $store.state.showTabbar}" :style="{background:bg}">
      <transition name="van-slide-down">
        <van-row justify="space-between" align="center" class="head" :style="{height: height + 'px', color:fg}" v-show="state.isTop && !nonavbar && !notitle">
          <van-col span="16">
            <div class="title" :style="{color:fg}" @click="$emit('click-title')">
              {{title}}
              <div class="subtitle">{{subtitle}}</div>
            </div>
          </van-col>
          <van-col span="8">
            <div class="extra"><slot name="extra"></slot></div>
          </van-col>
        </van-row>
      </transition>
      <slot></slot>
    </div>

  </div>
</template>

<style scoped>
.container {
  width: 100%;
  height: 100%;
}

.navbar {
  background: transparent;
}
.navbar.notTop {
  background: #FCFCFC;
  border-bottom: solid 1px #F6F6F6;
}
.navbar.noTitle {
  background: transparent;
  border: 0;
}

.page {
  position: relative;
  top: -45px;
  padding-top: 45px;
  min-height: calc(100% - 50px);
  display: block;
}
.page.notTop {
  top: 0;
}
.page.noNavbar {
  top: 0;
  padding-top: 0px;
}
.page.noTitle {
  padding-top: 0px;
}
.page.hasBar {
  margin-bottom: 50px;
}

.head {
  background: transparent;
  color: #000;
  overflow: hidden;
}
.head .title {
  min-height: 40px;
  padding: 0px 0px 20px 20px;
  font-size: 28px;
  font-weight: 600;  
  text-align: left;
}
.head .subtitle {
  padding-top: 5px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  opacity: 0.8;  
}
.head .extra {
  float: right;
}
</style>

<script>
import { reactive } from 'vue'

export default {
  props: {
    nonavbar: {
      type: Boolean,
      default: false,
    },
    notitle: {
      type: Boolean,
      default: false,
    },
    noback: {
      type: Boolean,
      default: false,
    },
    title: String,
    subtitle: String,
    height: String,
    bg: String,
    fg: String,
  },

  setup(){
    const state = reactive({
      isTop: true,
    });

    return {
      state
    };
  },

  methods: {
    onClickLeft(){
      if (typeof this.$slots.back == "function")
        return this.$emit("click-back");
      console.log("<<< BACK");
      return this.$router.back();
    }
  }
}
</script>
